<template>
  <v-container
    class="ma-0 d-flex flex-row align-start pa-0 align-stretch"
    fluid
  >
    <v-row cols="12">
      <v-col
        class="ma-0 pt-0"
        md="2"
      >
        <v-card
          tile
          height="100vh"
          :width="cardWidth"
        >
          <v-navigation-drawer
            class="pa-1"
            v-model="drawer"
            color="primary"
            absolute
            permanent
            left
            :mini-variant.sync="mini"
            dark
          >
            <template v-slot:prepend>
              <v-list-item class="px-2">
                <v-list-item-avatar>
                  <img :src="userInfo.sellerImg ? userInfo.sellerImg : userInfo.buyerImg">
                </v-list-item-avatar>

                <v-list-item-content>
                  <v-list-item-title>{{ userInfo.firstName }}</v-list-item-title>
                </v-list-item-content>

                <v-btn
                  icon
                  @click.stop="mini = !mini"
                >
                  <v-icon>mdi-chevron-left</v-icon>
                </v-btn>
              </v-list-item>
            </template>

            <v-divider></v-divider>

            <v-list>
              <v-list-item @click="showUserProfile()">
                <v-list-item-icon>
                  <v-icon>mdi-account-box</v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                  <v-list-item-title>ข้อมูลสมาชิก</v-list-item-title>
                </v-list-item-content>
              </v-list-item>

              <v-list-item link>
                <v-list-item-icon>
                  <v-icon>mdi-basket</v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                  <v-list-item-title>เพิ่มสินค้า</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list>
          </v-navigation-drawer>
        </v-card>
      </v-col>
      <v-col v-if="isUserProfile.seller">
        <v-container
          class="mr-auto"
          fluid
        >
          <v-card
            class="mx-auto"
            max-width="400"
          >
            <v-layout
              column
              align-center
            >
              <v-avatar
                size='150'
                class="my-3"
              >
                <img :src="userInfo.sellerImg">
              </v-avatar>
              <v-card-text class="headline text-md-center py-0">{{ userInfo.firstName + userInfo.lastName }}</v-card-text>
              <v-card-text class="text-md-center pt-1 pb-4">{{ userInfo.nameTypeOfSeller }}</v-card-text>
            </v-layout>

            <v-card-text class="text--primary text-center ma-2">
              <v-row>เลขบัตรประชาชน : {{ userInfo.idCard || '-' }} หมายเลขโทรศัพท์ {{ userInfo.phoneNumber || '-' }}</v-row>
              <v-row>จังหวัด {{ userInfo.provinceName || '-' }} อำเภอ {{ userInfo.amphurName || '-' }}</v-row>
              <v-row>หมู่ {{ userInfo.moo || '-' }} ถนน {{ userInfo.road || '-' }} ตำบล {{ userInfo.tambolName || '-' }}</v-row>
              <v-row>พื้นที่โครงการ {{ userInfo.areaName || '-' }} </v-row>
              <v-row>ประเภทสินค้า {{ userInfo.productType || '-' }}</v-row>
              <v-row>สถานะสมาชิก : {{ userInfo.productType === '1' ? 'ได้รับการยืนยันแล้ว' : 'ยังไม่ได้รับการยืนยัน' }}</v-row>
            </v-card-text>

            <v-card-actions>
              <v-btn
                color="orange"
                text
              >
                แก้ไขข้อมูล
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-container>
      </v-col>
      <v-col v-if="isUserProfile.buyer">
        <v-container
          class="mr-auto"
          fluid
        >
          <v-card
            class="mx-auto"
            max-width="400"
          >
            <v-layout
              column
              align-center
            >
              <v-avatar
                size='150'
                class="my-3"
              >
                <img :src="userInfo.buyerImg">
              </v-avatar>
              <v-card-text class="headline text-md-center py-0">{{ userInfo.firstName + ' ' + userInfo.lastName }}</v-card-text>
              <v-card-text class="text-md-center pt-1 pb-4">{{ userInfo.typeOfMember === 'Normal' ? 'สมาชิกทั่วไป' : 'สมาชิกผู้ซื้อ' }}</v-card-text>
            </v-layout>

            <v-card-text class="text--primary">
              <div>เลขบัตรประชาชน : {{ userInfo.idCard }}</div>

              <div>Whitsunday Island, Whitsunday Islands</div>
            </v-card-text>

            <v-card-actions>
              <v-btn
                color="orange"
                text
                :to="{ name: 'EditProfile' }"
              >
                แก้ไขข้อมูล
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-container>
      </v-col>
    </v-row>

  </v-container>
</template>

<script>
import axios from "axios";
import app from "../functions/app";

export default {
  data() {
    return {
      isUserProfile: {
        seller: false,
        buyer: false,
      },
      mini: false,
      drawer: true,
      userInfo: [],
      buyerForm: {
        prefix: "",
        firstName: "",
        lastName: "",
        username: "",
        phoneNumber: "",
        email: "",
        idCard: "",
        password: "",
        img: [],
        provinceCode: "",
        typeOfMember: "",
        amphurCode: "",
        tambolCode: "",
        lineId: "",
        postcode: "",
        facebookName: "",
        address: "",
        moo: "",
        road: "",
        questionOfVisit: "",
        questionOfObjective: "",
      },
    };
  },
  computed: {
    cardWidth() {
      return this.mini ? 56 : 256;
    },
  },
  methods: {
    getUserInfo() {
      let username = new FormData();
      username.append("username", localStorage.user);
      if (localStorage.typeOfMember === "Seller") {
        username.append("table", "SellerMember_MK");
        axios
          .post(
            "https://farmtd.hrdi.or.th/farmdd/admin/api/member/postFindSellerInfo.php",
            username
          )
          .then((res) => {
            if (res.data.sellerUser !== 0) {
              this.userInfo = res.data.sellerUser[0];
              this.userInfo.sellerImg = `https://farmtd.hrdi.or.th/farmdd/admin/upload/profile/${res.data.sellerUser[0].sellerImg}`;
              this.isUserProfile.seller = true;
              console.log("userInfo", this.userInfo);
            }
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        axios
          .post(
            "https://farmtd.hrdi.or.th/farmdd/admin/api/member/postFindUserInfo.php",
            username
          )
          .then((res) => {
            if (res.data.buyerUser !== 0) {
              this.userInfo = res.data.buyerUser[0];
              this.userInfo.buyerImg = `https://farmtd.hrdi.or.th/farmdd/admin/upload/profile/${res.data.buyerUser[0].buyerImg}`;
              this.isUserProfile.buyer = true;
              console.log("userInfo", this.userInfo);
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    showUserProfile() {
      if (localStorage.typeOfMember === "Seller") {
        this.isUserProfile.seller = true;
      } else {
        this.isUserProfile.buyer = true;
      }
    },
    checkToken() {
      axios
        .post("https://farmtd.hrdi.or.th/farmdd/admin/api/checkJwt.php", {
          headers: { Authorization: "1234" },
        })
        .then((res) => console.log(res));
    },
  },
  mounted() {
    this.getUserInfo();
    this.checkToken();
    app.getPrefix();
  },
};
</script>


