                color="primary"
              >
                <v-toolbar-title>สมัครสมาชิกสำหรับสมาชิกทั่วไป / ผู้เข้ามาซื้อสินค้า</v-toolbar-title>
              </v-toolbar>
              <v-card-text>
                <v-col cols="12">
                  <v-row>
                    <v-col>
                      <v-select
                        prepend-icon="mdi-account-box"
                        v-model="buyerForm.prefix"
                        item-text="prefixNameTh"
                        item-value="idPrefix"
                        :items="prefix"
                        :rules="[v => !!v || 'กรุณาเลือกคำนำหน้า']"
                        label="คำนำหน้า*"
                        required
                      >*</v-select>
                    </v-col>
                    <v-col>
                      <v-text-field
                        prepend-icon="mdi-account"
                        name="firstName"
                        label="ชื่อ*"
                        type="text"
                        v-model="buyerForm.firstName"
                        :rules="firstNameRules"
                      ></v-text-field>
                    </v-col>
                    <v-col>
                      <v-text-field
                        name="lastName"
                        label="นามสกุล*"
                        type="text"
                        v-model="buyerForm.lastName"
                        :rules="lastNameRules"
                      ></v-text-field>
                    </v-col>
                  </v-row>

                  <v-row
                    md="12"
                    fluid
                  >
                    <v-col
                      md="6"
                      class="mr-0"
                    >
                      <p class="font-weight-black grey--text text--darken-4">เพศ*</p>
                      <v-radio-group
                        row
                        v-model="buyerForm.gender"
                        :rules="[v => !!v || 'กรุณาระบุเพศ']"
                      >
                        <v-radio
                          label="ชาย"
                          value="ชาย"
                        ></v-radio>
                        <v-radio
                          label="หญิง"
                          value="หญิง"
                        ></v-radio>
                      </v-radio-group>
                    </v-col>
                  </v-row>

                  <v-row
                    align="center"
                    justify="center"
                  >
                    <v-img
                      v-if="imagePreviewURL"
                      :src="imagePreviewURL"
                      :lazy-src="imagePreviewURL"
                      aspect-ratio="1"
                      class="grey lighten-2 mb-2"
                      max-width="500"
                      max-height="300"
                    >
                      <template v-slot:placeholder>
                        <v-row
                          class="fill-height ma-0"
                          align="center"
                          justify="center"
                        >
                          <v-progress-circular
                            indeterminate
                            color="grey lighten-5"
                          ></v-progress-circular>
                        </v-row>
                      </template>
                    </v-img>
                  </v-row>

                  <v-file-input
                    :rules="imgRules"
                    accept="image/png, image/jpeg, image/bmp"
                    placeholder="เลือกรูปภาพประจำตัว"
                    label="รูปภาพโปรไฟล์"
                    v-model="buyerForm.img"
                    prepend-icon="mdi-camera"
                    @change="previewImg"
                  ></v-file-input>
                  <v-text-field
                    prepend-icon="mdi-account-outline"
                    v-model="buyerForm.username"
                    @keyup="checkUsername(buyerForm.username)"
                    type="text"
                    :rules="[!isUsernameExist || 'Username ถูกใช้งานไปแล้ว', (v) => !!v || 'กรุณากรอก Username']"
                    label="Username*"
                    required
                  ></v-text-field>

                  <v-row md="6">
                    <v-col>
                      <v-text-field
                        prepend-icon="mdi-lock"
                        v-model="buyerForm.password"
                        type="password"
                        counter
                        :rules="passwordRules"
                        label="รหัสผ่าน*"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col>
                      <v-text-field
                        prepend-icon="mdi-lock"
                        v-model="confirmPassword"
                        type="password"
                        counter
                        :rules="[(buyerForm.password === confirmPassword) || 'รหัสผ่านไม่ตรงกัน', (v) => !!v || 'กรุณายืนยันรหัสผ่าน']"
                        label="ยืนยันรหัสผ่าน*"
                        required
                      ></v-text-field>
                    </v-col>
                  </v-row>

                  <v-row md="12">
                    <v-col>
                      <v-text-field
                        prepend-icon="mdi-email"
                        v-model="buyerForm.email"
                        type="email"
                        label="อีเมล"
                        required
                      ></v-text-field>
                    </v-col>
                  </v-row>

                  <v-row md="6">
                    <v-col>
                      <v-text-field
                        prepend-icon="mdi-phone"
                        v-model="buyerForm.phoneNumber"
                        counter
                        maxlength="10"
                        :rules="phoneRules"
                        label="หมายเลขโทรศัพท์*"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col>
                      <v-text-field
                        prepend-icon="mdi-account-details"
                        v-model="buyerForm.idCard"
                        counter
                        maxlength="13"
                        label="หมายเลขบัตรประชาชน"
                        required
                      ></v-text-field>
                    </v-col>
                  </v-row>

                  <v-row md="6">
                    <v-col>
                      <v-text-field
                        prepend-icon="mdi-phone-plus"
                        v-model="buyerForm.lineId"
                        type="text"
                        label="ไลน์ไอดี"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col>
                      <v-text-field
                        prepend-icon="mdi-facebook"
                        v-model="buyerForm.facebookName"
                        type="text"
                        label="Facebook"
                        required
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row md="6">
                    <v-col>
                      <v-text-field
                        label="บ้านเลขที่"
                        type="text"
                        v-model="buyerForm.address"
                      ></v-text-field>
                    </v-col>
                    <v-col>
                      <v-text-field
                        label="หมู่ (ถ้ามี)"
                        type="text"
                        v-model="buyerForm.moo"
                      ></v-text-field>
                    </v-col>
                    <v-col>
                      <v-text-field
                        label="ถนน"
                        type="text"
                        v-model="buyerForm.road"
                      ></v-text-field>
                    </v-col>
                    <v-col>
                      <v-text-field
                        label="รหัสไปรณีย์"
                        type="text"
                        v-model="buyerForm.postcode"
                      ></v-text-field>
                    </v-col>
                  </v-row>

                  <v-row md="4">
                    <v-col>
                      <v-select
                        prepend-icon="mdi-map-marker-circle"
                        v-model="buyerForm.provinceCode"
                        item-text="name"
                        item-value="code"
                        :items="allProvince"
                        :rules="[v => !!v || 'กรุณาเลือกจังหวัด']"
                        @change="findAmphurBuyer(buyerForm.provinceCode)"
                        label="จังหวัด*"
                        required
                      ></v-select>
                    </v-col>
                    <v-col>
                      <v-select
                        v-model="buyerForm.amphurCode"
                        item-text="ampT"
                        item-value="ampCode"
                        :items="buyerAmphur"
                        :rules="[v => !!v || 'กรุณาเลือกอำเภอ']"
                        @change="findTambolBuyer(buyerForm.provinceCode, buyerForm.amphurCode)"
                        label="อำเภอ*"
                        required
                      ></v-select>
                    </v-col>
                    <v-col>
                      <v-select
                        v-model="buyerForm.tambolCode"
                        item-text="tamT"
                        item-value="tamCode"
                        :items="buyerTambol"
                        :rules="[v => !!v || 'กรุณาเลือกตำบล']"
                        label="ตำบล*"
                        required
                      ></v-select>
                    </v-col>
                  </v-row>

                  <v-row
                    md="12"
                    fluid
                  >
                    <v-col
                      md="6"
                      class="mr-0"
                    >
                      <p class="font-weight-black grey--text text--darken-4">ประเภทสมาชิก*</p>
                      <v-radio-group
                        row
                        v-model="buyerForm.typeOfMember"
                        :rules="[v => !!v || 'กรุณาเลือกประเภทสมาชิก']"
                      >
                        <v-radio
                          label="สมาชิกทั่วไป"
                          value="Normal"
                        ></v-radio>
                        <v-radio
                          label="สมาชิกผู้ซื้อ"
                          value="Buyer"
                        ></v-radio>
                      </v-radio-group>
                    </v-col>
                  </v-row>

                  <v-row
                    md="12"
                    fluid
                  >
                    <v-col
                      md="6"
                      class="mr-0"
                    >
                      <p class="font-weight-black grey--text text--darken-4">คุณรู้จักเวบไซด์ของดีบนดอยได้อย่างไร*</p>
                      <v-radio-group
                        @change="questionBuyer(buyerForm.questionOfVisit)"
                        v-model="buyerForm.questionOfVisit"
                        :rules="[v => !!v || 'กรุณาเลือกตัวเลือก']"
                      >
                        <v-radio
                          label="ค้นหาจาก Google"
                          value="ค้นหาจาก Google"
                        ></v-radio>
                        <v-radio
                          label="เข้ามาในเวบไซด์ของสถาบันวิจัยและพัฒนาพื้นที่สูง"
                          value="เข้ามาในเวบไซด์ของสถาบันวิจัยและพัฒนาพื้นที่สูง"
                        ></v-radio>
                        <v-radio
                          label="เพื่อน / คนรู้จักแนะ"
                          value="เพื่อน / คนรู้จักแนะ"
                        ></v-radio>
                        <v-radio
                          label="อื่น ๆ"
                          value="อื่น ๆ"
                        ></v-radio>
                      </v-radio-group>
                      <v-text-field
                        outlined
                        v-if="showEtc.visit"
                        type="text"
                        label="เหตุผลอื่นๆ"
                        :rules="[v => !!v || 'กรุณากรอกเหตุผล']"
                        v-model="buyerForm.questionOfVisit"
                      ></v-text-field>
                    </v-col>
                    <v-col md="6">
                      <p class="font-weight-black grey--text text--darken-4">วัตถุประสงค์ที่สมัครสมาชิก*</p>
                      <v-radio-group
                        @change="questionObjBuyer(buyerForm.questionOfObjective)"
                        v-model="buyerForm.questionOfObjective"
                        :rules="[v => !!v || 'กรุณาเลือกตัวเลือก']"
                      >
                        <v-radio
                          label="เข้ามาเยี่ยมชม"
                          value="เข้ามาเยี่ยมชม"
                        ></v-radio>
                        <v-radio
                          label="เข้ามาซื้อผลผลิต"
                          value="เข้ามาซื้อผลผลิต"
                        ></v-radio>
                        <v-radio
                          label="เข้ามาอ่าน ติดตามข้อมูลข่าวสาร"
                          value="เข้ามาอ่าน ติดตามข้อมูลข่าวสาร"
                        ></v-radio>
                        <v-radio
                          label="อื่น ๆ"
                          value="อื่น ๆ"
                        >
                        </v-radio>
                        <v-text-field
                          class="mt-3"
                          outlined
                          v-if="showEtc.objective"
                          type="text"
                          label="เหตุผลอื่นๆ"
                          :rules="[v => !!v || 'กรุณากรอกเหตุผล']"
                          v-model="buyerForm.questionOfObjective"
                        ></v-text-field>
                      </v-radio-group>
                    </v-col>
                  </v-row>
                </v-col>
              </v-card-text>
              <v-card-actions>
                <v-btn
                  block
                  color="primary"
                  @click="checkBuyerRegister"
                  :disabled="!isFormValid"
                >ยืนยันสมัครสมาชิก</v-btn>
              </v-card-actions>
            </v-card>
          </v-form>
        </v-row>
        <!-- End Buyer Form !-->
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import axios from "axios";
import app from "../functions/app";

export default {
  data: () => ({
    isFormValid: false,
    prefix: [],
  }),
  methods: {
    getPrefix() {
      axios
        .get("https://farmtd.hrdi.or.th/farmdd/admin/api/getPrefix.php")
        .then((res) => {
          this.prefix = res.data.prefix;
          console.log("prefix", this.prefix);
        });
    },
  },
  mounted() {
    app.checkLogin();
    app.getPrefix();
  },
};
</script>


